<template>
  <div class="addressadd">
    <!-- head部分 begin -->
    <headerBar :header-data="headerData" />
    <!-- head部分 end -->
    <div class="container">

      <!-- 新增发票地址 begin -->
      <div class="content">
        <div class="common-main box">

          <div v-if="invoicetype==='ZZSZYFP'">
            <div class="title">
              <div class="title-left">
                <h2>增值税专用发票</h2>
              </div>
            </div>
            <div class="content common-main">
              <van-field v-model="header" v-validate="'required'" required name="fptt" :error-message="errors.first('fptt')" label="发票抬头" label-width="110px" placeholder="请输入发票抬头" />
              <div class="spe-list">
                <div class="spe-left">
                  <span class="tit">发票内容</span>
                  <span class="info">
                    <van-radio-group v-model="invoicecontent">
                      <van-radio name="MX">明细</van-radio>
                      <van-radio name="DNPJ">电脑配件</van-radio>
                      <van-radio name="HC">耗材</van-radio>
                      <van-radio name="BGYP">办公用品</van-radio>
                    </van-radio-group>
                  </span>
                </div>
              </div>
              <van-field
                v-model="company_code"
                v-validate="'required|sbmCheck'"
                label="纳税人识别码"
                label-width="110px"
                name="nsrsbm"
                required
                :error-message="errors.first('nsrsbm')"
                placeholder="请输入纳税人识别码"
              />
              <van-field
                v-model="company_address"
                v-validate="'required'"
                label="注册地址"
                label-width="110px"
                name="zcdd"
                required
                :error-message="errors.first('zcdd')"
                placeholder="请输入注册地址"
              />
              <van-field
                v-model="company_phone"
                v-validate="'required|phone'"
                label="注册电话"
                label-width="110px"
                name="zcdh"
                required
                :error-message="errors.first('zcdh')"
                placeholder="请输入注册电话"
              />
              <van-field
                v-model="company_bank"
                v-validate="'required'"
                label="开户银行"
                label-width="110px"
                name="khyh"
                required
                :error-message="errors.first('khyh')"
                placeholder="请输入开户银行"
              />
              <van-field
                v-model="company_bankname"
                v-validate="'required|yhzhCheck'"
                label="银行账户"
                label-width="110px"
                name="yhzh"
                required
                :error-message="errors.first('yhzh')"
                placeholder="请输入银行账户"
              />
              <van-field
                v-model="address"
                v-validate="'required|address'"
                label="所在地区"
                placeholder="请选择所在地区"
                name="address"
                label-width="110px"
                required
                is-link
                readonly
                :error-message="errors.first('address')"
                @click="showPopup"
              />
              <van-field
                v-model="invoiceaddress"
                v-validate="'required'"
                label="所在街道"
                rows="2"
                name="jtdz"
                :error-message="errors.first('jtdz')"
                autosize
                type="textarea"
                required
                label-width="110px"
                maxlength="50"
                placeholder="请填写街道、楼道号等"
              />
            </div>
            <div class="bt-group">
              <van-button type="info" block round size="small" @click="saveInvoice('ZZSZYFP')">保存</van-button>
            </div>
          </div>
          <div v-if="invoicetype==='PTFP'">
            <div class="title">
              <div class="title-left">
                <h2>普通发票</h2>
              </div>
            </div>
            <div class="content common-main">
              <van-field v-model="header" :error-message="message1" label="发票抬头" label-width="110px" required placeholder="请输入发票抬头" @input="check1()" @blur="check1()" />
              <div class="spe-list">
                <div class="spe-left">
                  <span class="tit">发票内容</span>
                  <span class="info">
                    <van-radio-group v-model="invoicecontent">
                      <van-radio name="MX">明细</van-radio>
                      <van-radio name="DNPJ">电脑配件</van-radio>
                      <van-radio name="HC">耗材</van-radio>
                      <van-radio name="BGYP">办公用品</van-radio>
                    </van-radio-group>
                  </span>
                </div>
              </div>
              <van-field v-model="company_code" :error-message="message2" label="纳税人识别码" label-width="110px" required placeholder="请输入纳税人识别码" @blur="check2()" @input="check2()" />
            </div>
            <div class="bt-group">
              <van-button type="info" block round size="small" @click="savePtInvoice('PTFP')">保存</van-button>
            </div>
          </div>

        </div>
      </div>

      <!-- 新增发票地址 end -->
    </div>
    <!-- 地址弹出层 beign -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '70%' }"
    >
      <!-- <van-area ref="myArea" :area-list="areaList" :value="addressShow" :columns-num="3" @change="onChange" @confirm="onConfirm" @cancel="onCancel" /> -->
      <XyAddress @getCheckResult="getCheckResult" />
    </van-popup>
    <!-- 地址弹出层 end -->
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import XyAddress from '@/components/address/address.vue'
import { saveInvoice } from '@/store/module.js'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'InvoiceEdit',
  components: {
    headerBar,
    XyAddress
  },
  // mixins: [pageRouter],
  data() {
    return {
      headerData: {
        title: '修改发票模板',
        leftUrl: 'back',
        leftText: ' '
      },
      message1: '',
      message2: '',
      addressShow: '', // 地区地址code码
      address: '', // 地区地址
      invoice: '', // 发票信息
      header: '', // 发票抬头
      company_code: '', // 纳税人识别码
      company_address: '', // 注册地址
      company_phone: '', // 注册电话
      company_bank: '', // 开户银行
      company_bankname: '', // 银行账户
      szjd: '', // 所在街道
      invoiceprovice: '', // 省份
      invoicecity: '', // 市
      invoicecounty: '', // 县
      invoicetown: '', // 区/镇
      invoiceaddress: '', // 详细地址
      checked: true,
      show: false,
      radio: '1',
      addressmodel: '',
      id: '',
      invoicecontent: '', // 发票内容
      jgbh: ''
    }
  },
  created() {
    if (this.$route.params) {
      this.invoice = this.$route.params.invoice
      if (this.invoice) {
        if (this.invoice.invoicetype === 'ZZSZYFP') {
          this.id = this.invoice.id
          this.jgbh = this.invoice.jgbh
          this.header = this.invoice.header
          this.invoicetype = this.invoice.invoicetype
          this.company_code = this.invoice.company_code
          this.company_address = this.invoice.company_address
          this.company_phone = this.invoice.company_phone
          this.company_bank = this.invoice.company_bank
          this.company_bankname = this.invoice.company_bankname
          this.invoicecontent = this.invoice.invoicecontent
          this.invoiceprovice = this.invoice.invoiceprovice
          this.invoicecity = this.invoice.invoicecity
          this.invoicecounty = this.invoice.invoicecounty
          this.invoicetown = this.invoice.invoicetown
          this.invoiceaddress = this.invoice.invoiceaddress
          this.address = this.invoice.area
        }
        if (this.invoice.invoicetype === 'PTFP') {
          this.id = this.invoice.id
          this.invoicetype = this.invoice.invoicetype
          this.header = this.invoice.header
          this.company_code = this.invoice.company_code
          this.invoicecontent = this.invoice.invoicecontent
        }
      }
    }
  },
  methods: {
    // 获取地址组件返回值
    getCheckResult(sheng, shi, xian, xiang, state) {
      if (!state) {
        this.show = false
      }
      if (sheng) {
        this.address = sheng.text
        this.invoiceprovice = sheng.value
      }
      if (shi) {
        this.address = this.address + '/' + shi.text
        this.invoicecity = shi.value
      }
      if (xian) {
        this.address = this.address + '/' + xian.text
        this.invoicecounty = xian.value
      }
      if (xiang) {
        this.address = this.address + '/' + xiang.text
        this.invoicetown = xiang.value
      }
    },
    saveInvoice(type) {
      this.$validator.validate().then(result => {
        if (result) {
          saveInvoice.data = {
            id: this.id,
            invoicetype: type,
            header: this.header,
            company_code: this.company_code,
            company_address: this.company_address,
            company_phone: this.company_phone,
            company_bank: this.company_bank,
            company_bankname: this.company_bankname,
            invoicecontent: this.invoicecontent,
            invoiceprovice: this.invoiceprovice,
            invoicecity: this.invoicecity,
            invoicecounty: this.invoicecounty,
            invoicetown: this.invoicetown,
            invoiceaddress: this.invoiceaddress,
            jgbh: this.jgbh
          }
          this.asyncApiModule(saveInvoice).then(res => {
            this.$toast('保存发票信息成功')
            this.$router.back()
          })
            .catch(() => {
              this.loading = false
            })
        }
      })
    },
    check1() {
      if (this.header) {
        this.message1 = ''
        return true
      } else {
        this.message1 = '请输入发票抬头'
        return false
      }
    },
    check2() {
      if (this.company_code) {
        if (!/[0-9A-HJ-NPQRTUWXY]{2}\d{6}[0-9A-HJ-NPQRTUWXY]{10}/.test(this.company_code)) {
          this.message2 = '请输入正确的纳税人识别码'
          return false
        }
        this.message2 = ''
        return true
      } else {
        this.message2 = '请输入纳税人识别码'
        return false
      }
    },
    savePtInvoice(type) {
      if (this.check1() & this.check2()) {
        saveInvoice.data = {
          id: this.id,
          invoicetype: type,
          header: this.header,
          company_code: this.company_code,
          invoicecontent: this.invoicecontent,
          jgbh: this.jgbh
        }
        this.asyncApiModule(saveInvoice).then(res => {
          this.$toast('保存发票信息成功')
          this.$router.back()
        })
          .catch(() => {
            this.loading = false
          })
      }
    },
    goBack() {
      this.$router.back()
    },
    showPopup() {
      this.show = true
    },
    // value=0改变省，1改变市，2改变区
    onChange(picker, index, value) {
      const val = picker.getValues()
      console.log(val)// 查看打印
      let areaName = ''
      for (var i = 0; i < val.length; i++) {
        areaName = areaName + (i === 0 ? '' : '/') + val[i].name
      }
      this.addressmodel = areaName
    },
    // 确定选择城市
    onConfirm(val) {
      console.log('val-----', val)
      this.invoiceprovice = val[0].name
      this.invoicecity = val[1].name
      this.invoicecounty = val[2].name
      this.address = val[0].name + '/' + val[1].name + '/' + val[2].name
      this.addressShow = val[2].code
      this.show = false// 关闭弹框
    },
    // 取消选中城市
    onCancel() {
      this.show = false
      this.$refs.myArea.reset()// 重置城市列表
    }
  }
}
</script>

<style lang="css" scoped>

</style>
